.save {
  align-items: center;
  padding: 4px 14px;
  border-radius: 6px;
  border: 2px solid black;
  color: #fff;
  background: lightgreen;
  margin-left: 10px;
  width: 100px;
  cursor: pointer;
}

.save:hover {
  border: 2px solid rgb(47, 168, 255);
  outline: 0;
}

.save:active {
  color: rgb(47, 168, 255);
}
